  html {
  background-image: linear-gradient(to bottom, #3f0512, #650019, #8d001b, #b50018, #dc000a);
  background-repeat: no-repeat;
  text-align: center;
  color: white;
  height: auto;}
  
  .container{
    width: 750px;
    margin: 40px auto 25px auto;
  }
  
  h1{
    font-family: 'Indie Flower';
    font-size: 10vh;
  }
  h2{
    font-size: 5vh;
    font-family: 'Indie Flower';
    /* text-decoration: underline; */
  }
  .lenght{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .breakSession {
    font-family: 'Indie Flower';
    font-size: 4vh;
    font-weight: bold;
    width: 40px;
    height: 30px;
    text-align: center;
    color: white;
   
    
  }
  .timer{
    height: 55vh;
    width: 55vh;
    /* background-color: #bbb; */
    border-radius: 50%;
    border: 4px solid #ffff;
    /* display: flex; */
    /* justify-content: center; */
    -webkit-flex-direction: column;
            flex-direction: column;
    /* align-items: center;
    justify-self: center; */
    margin: 0px auto 15px auto;  
  }
  .timer-label{
    font-size: 6vh;
    /* margin-top: 30px;
    display: flex;
    align-items: end;
    justify-content: center; */
  }
  .time-left{
    font-size: 20vh;
    margin-top: 10px;
    font-weight: lighter;
    
    
  }
  .incre-decre{
    display: -webkit-flex;
    display: flex;
    text-align: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    font-family: 'Indie Flower'
  }
  Button {
    box-shadow:inset 0px 1px 0px 0px #9fb4f2;
    background:linear-gradient(to bottom, #7892c2 5%, #476e9e 100%);
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#7892c2', endColorstr='#476e9e',GradientType=0);
    background-color:#7892c2;
    border-radius:6px;
    border:1px solid #4e6096;
    display:inline-block;
    cursor:pointer;
    color:#ffffff;
    font-family:Arial;
    font-size:15px;
    font-weight:bold;
    padding:6px 24px;
    text-decoration:none;
    text-shadow:0px 1px 0px #283966;
  }
  Button:hover {
    background:linear-gradient(to bottom, #476e9e 5%, #7892c2 100%);
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#476e9e', endColorstr='#7892c2',GradientType=0);
    background-color:#476e9e;
  }
  Button:active {
    position:relative;
    top:1px;
  }
  .start-reset{
    width: 300px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin: 15px auto 20px auto;
    
  }
  .myButton {
    box-shadow:inset 0px 1px 0px 0px #9fb4f2;
    background:linear-gradient(to bottom, #7892c2 5%, #476e9e 100%);
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#7892c2', endColorstr='#476e9e',GradientType=0);
    background-color:#7892c2;
    border-radius:30%;
    border:1px solid #4e6096;
    display:inline-block;
    cursor:pointer;
    color:#ffffff;
    font-family:Arial;
    font-size:15px;
    font-weight:bold;
    padding:15px 32px;
    text-decoration:none;
    text-shadow:0px 1px 0px #283966;
  }
  
  footer{
    font-size: 2vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  
  @media only screen and (max-width: 760px){
    .container{
    width: 650px;
    margin: 40px auto 25px auto;
  }
    h1{
    font-family: 'Indie Flower';
    font-size: 8vh;
  }
    h2{
    font-size: 4vh;
    /* text-decoration: underline; */
  }
  }
  @media only screen and (max-width: 480px){
    .container{
    width: 400px;
    height: 450px;
    margin: 40px auto 40px auto;
  }
    h1{
    font-family: 'Indie Flower';
    font-size: 6vh;
  }
    h2{
    font-size: 3vh;
    /* text-decoration: underline; */
  }
    .timer-label{
    height: 40vh;
    width: 40vh;
    /* background-color: #bbb; */
    border-radius: 50%;
    border: 3px solid #ffff;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    justify-self: center;
    margin: 0px auto 15px auto;  
  }
    .time-left{
    font-size: 15vh
  }
    .start-reset{
    width: 300px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin: 15px auto 20px auto;
    
  }
  }
  

